<template>
  <div class="step">
    <Steps :current="step">
      <Step title="支出信息" content="店铺支出信息"></Step>
      <Step title="销售信息" content="店铺销售信息"></Step>
      <Step title="补贴信息" content="店铺补贴信息"></Step>
    </Steps>
  </div>
</template>

<script>
  export default {
    props: {
      step: Number
    }
  };
</script>

<style scoped>
  .step {
    margin-top: 20px;
  }
</style>



