<template>
  <div class="information-form">

    <div class="basic-information" v-show="step===0">
      <Form :label-width="100">
        <h1>支出信息</h1>
        <FormItem label="名称">
          <Input v-model="formData.name"></Input>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="店铺面积">
          <Slider v-model="formData.area" show-input :max="200"></Slider>
        </FormItem>
        <FormItem label="单位面积月租金">
          <InputNumber v-model="formData.costPerArea" :step="100" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="店铺总月租金">
          <span class="total">{{thousands(formData.area*formData.costPerArea)}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="店员数">
          <Slider v-model="formData.personNumber" show-input :max="20"></Slider>
        </FormItem>
        <FormItem label="单人月成本">
          <InputNumber v-model="formData.costPerPerson" :step="1000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="人工小计">
          <span class="total">{{thousands(formData.personNumber*formData.costPerPerson)}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="杂费">
          <InputNumber v-model="formData.otherCost" :step="1000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="杂费合计">
          <span class="total">{{thousands(formData.otherCost)}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="支出合计">
          <span class="total">{{thousands(formData.area*formData.costPerArea+formData.personNumber*formData.costPerPerson+formData.otherCost)}}</span>
        </FormItem>
        <Row type="flex" justify="center">
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div>

    <div class="sale" v-show="step===1">
      <Form :label-width="100">
        <h1>销售信息</h1>
        <FormItem label="销售总额">
          <InputNumber v-model="formData.sales" :step="10000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="销售总额">
          <span class="total">{{thousands(formData.sales)}}</span>
        </FormItem>
        <div class="divider"></div>
        <template v-for="(item,index) in formData.category">
          <div :key="index">
            <FormItem label="品类名称" required>
              <Row>
                <Col span='20'>
                <Select v-model="item.name" @on-select="show">
                  <Option v-for="category in categoryList" :key="category" :value="category">{{category}}</Option>
                </Select>
                </Col>
                <Col span='4'>
                <Button class="remove-btn" @click="removeCategory(index)">remove</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem label="销售占比">
              <Slider v-model="item.saleRate" show-input :disabled="item.isStable" @on-change="changeSaleRate(item)"></Slider>
            </FormItem>
            <FormItem label="销售额">
              <Row>
                <Col span="20">
                <span class="total">{{thousands(formData.sales*item.saleRate/100)}}</span>
                </Col>
                <Col span="4">
                <span>固定</span>
                <i-switch v-model="item.isStable" @on-change="toogleStableBtn(index,item)">
                  <Icon type="android-done" slot="open"></Icon>
                  <Icon type="android-close" slot="close"></Icon>
                </i-switch>
                </Col>
              </Row>
            </FormItem>
            <FormItem label="毛利率">
              <Slider v-model="item.grossProfitRate" show-input :max="15"></Slider>
            </FormItem>
            <FormItem label="毛利润">
              <span class="total">{{thousands(formData.sales*item.saleRate*item.grossProfitRate/10000)}}</span>
            </FormItem>
            <FormItem label="固返比例">
              <Slider v-model="item.stableReturnRate" show-input :max="20"></Slider>
            </FormItem>
            <FormItem label="固返额">
              <span class="total">{{thousands(formData.sales*item.saleRate*item.stableReturnRate/10000)}}</span>
            </FormItem>
            <FormItem label="月返比例">
              <Slider v-model="item.returnValuePerMonth" show-input :max="20"></Slider>
            </FormItem>
            <FormItem label="月返额">
              <span class="total">{{thousands(formData.sales*item.saleRate*item.returnValuePerMonth*formData.returnRatePerMonth/1000000)}}</span>
            </FormItem>
            <div class="divider"></div>
          </div>
        </template>
        <Row type="flex" justify="center">
          <Col span="12">
          <Button type="dashed" long @click="addCategory" icon="plus-round">Add category</Button>
          </Col>
        </Row>
        <div class="divider"></div>
        <FormItem label="毛利润总额">
          <span class="total">{{thousands(formData.category.reduce((sum,item)=>sum+formData.sales*item.saleRate*item.grossProfitRate/10000,0))}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="固返总额">
          <span class="total">{{thousands(formData.category.reduce((sum,item)=>sum+formData.sales*item.saleRate*item.stableReturnRate/10000,0))}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="月返比例">
          <Slider v-model="formData.returnRatePerMonth" show-input :max="100"></Slider>
        </FormItem>
        <FormItem label="月返总额">
          <span class="total">{{thousands(formData.category.reduce((sum,item)=>sum+formData.sales*item.saleRate*item.returnValuePerMonth*formData.returnRatePerMonth/1000000,0))}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="季返基数">
          <InputNumber v-model="formData.returnValuePerQuarter" :step="10000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="季返比例">
          <Slider v-model="formData.returnRatePerQuarter" show-input :max="100"></Slider>
        </FormItem>
        <FormItem label="月季返总额">
          <span class="total">{{thousands(formData.returnValuePerQuarter*formData.returnRatePerQuarter/300)}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="销售总收入">
          <span class="total">{{thousands(formData.category.reduce((sum, item) => sum + formData.sales*item.saleRate*item.grossProfitRate/10000 + formData.sales*item.saleRate*item.stableReturnRate/10000 + formData.sales*item.saleRate*item.returnValuePerMonth*formData.returnRatePerMonth/1000000 , formData.returnValuePerQuarter*formData.returnRatePerQuarter/300))}}</span>
        </FormItem>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div>

    <div class="other-information" v-show="step===2">
      <Form :label-width="100">
        <h1>补贴信息</h1>
        <FormItem label="季度店补">
          <InputNumber v-model="formData.allowancePerQuarter" :step="10000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="月季度店补总额">
          <span class="total">{{thousands(formData.allowancePerQuarter/3)}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="开业支持">
          <InputNumber v-model="formData.allowanceForStart" :step="1000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="开业支持总额">
          <span class="total">{{thousands(formData.allowanceForStart)}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="补贴总额">
          <span class="total">{{thousands(formData.allowancePerQuarter/3+formData.allowanceForStart)}}</span>
        </FormItem>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
        </Row>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      step: Number
    },
    data() {
      return {
        formData: {
          name: "默认店铺",
          area: 100,
          costPerArea: 600,
          personNumber: 8,
          costPerPerson: 5000,
          otherCost: 5000,
          sales: 600000,
          returnRatePerMonth: 90,
          returnValuePerQuarter: 40000,
          returnRatePerQuarter: 90,
          category: [],
          allowancePerQuarter: 25000,
          allowanceForStart: 7500
        },
        categoryList: [
          "DSC",
          "ILC",
          "Lens",
          "PV",
          "LCD",
          "MDR",
          "SPK",
          "DMP",
          "PS",
          "Mobile",
          "SB",
          "Others"
        ]
      };
    },
    methods: {
      changeStep(dir) {
        if (this.step === 1) {
          if (this.formData.category.some(item => item.name === "")) {
            this.$Message.error("品类名称不能为空");
          } else {
            this.$emit("changeStep", dir);
          }
        } else {
          this.$emit("changeStep", dir);
        }
      },
      changeFormData() {
        this.$emit("changeFormData", this.formData);
      },
      addCategory() {
        let sum = this.formData.category.reduce((sum, item) => {
          return sum + item.saleRate;
        }, 0);
        this.formData.category.push({
          name: "",
          grossProfitRate: 5,
          stableReturnRate: 5,
          returnValuePerMonth: 3,
          saleRate: Math.max(0, 100 - sum),
          isStable: false
        });
      },
      removeCategory(index) {
        this.formData.category.splice(index, 1);
      },
      thousands(num) {
        if (typeof num === "number" && num === num) {
          num = String(num);
          let pos = num.indexOf(".", 1),
            res = pos > 0 ? num.substring(0, pos) : num,
            result = [];
          if (res.length < 4) {
            result.unshift(res);
          } else {
            for (let i = res.length; i > 0; i -= 3) {
              if (i > 3) {
                result.unshift(res.substr(i - 3, 3));
              } else {
                result.unshift(res.substr(0, i));
              }
            }
          }
          return result.join(",");
        } else {
          return num;
        }
      },
      show() {
        console.log(this.formData.category);
      },
      toogleStableBtn(index, item) {
        if (this.formData.category[index].isStable) {
          let sum = this.formData.category.reduce((sum, item) => {
            return sum + item.saleRate;
          }, 0);
          if (sum > 100) {
            this.$Message.error("销售占比总数不能大于100%");
            setTimeout(() => {
              this.formData.category[index].isStable = false;
            }, 300);
            return;
          }
          if (this.formData.category[index].name === "") {
            this.$Message.error("名称不能为空");
            setTimeout(() => {
              this.formData.category[index].isStable = false;
            }, 300);
          }
        }
      },
      changeSaleRate(changeItem) {
        let notStableItems = this.formData.category.filter(
          item => !item.isStable && changeItem !== item
        );
        let sum = this.formData.category.reduce((sum, item) => {
          return sum + item.saleRate;
        }, 0);
        if (notStableItems && notStableItems.length === 1) {
          let notStableItem = notStableItems[0];
          notStableItem.saleRate = Math.max(
            0,
            100 + notStableItem.saleRate - sum
          );
        }
      }
    },
    mounted() {
      this.changeFormData();
    }
  };
</script>

<style scoped>
  h1 {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .mt-10 {
    margin-top: 20px;
  }
  .divider {
    width: 90%;
    margin: 20px auto;
    border-bottom: 2px dashed #f0f0f0;
  }
  .remove-btn {
    margin-left: 17px;
  }
  .total {
    font-size: 15px;
    font-weight: bold;
  }
</style>



