<template>
  <div>
    <div v-show="step===0" id="costChart" class="chart"></div>
    <div v-show="step===1||step===2" id="incomeChart" class="chart"></div>
  </div>
</template>

<script>
  import echarts from "echarts";

  export default {
    props: {
      formData: Object,
      step: Number
    },
    data() {
      return {
        costChart: null,
        incomeChart: null
      };
    },
    computed: {
      rent() {
        return this.formData.area * this.formData.costPerArea;
      },
      laborCost() {
        return this.formData.personNumber * this.formData.costPerPerson;
      },
      cost() {
        return this.rent + this.laborCost + this.formData.otherCost;
      },
      income() {
        if (this.formData.category) {
          return this.formData.category.reduce((sum, item) => {
            return (
              sum +
              this.formData.sales * item.saleRate * item.grossProfitRate / 10000 +
              this.formData.sales *
                item.saleRate *
                item.stableReturnRate /
                10000 +
              this.formData.sales *
                item.saleRate *
                item.returnValuePerMonth *
                this.formData.returnRatePerMonth /
                1000000
            );
          }, 0);
        } else {
          return 0;
        }
      },
      allowance() {
        return (
          this.formData.allowancePerQuarter / 3 +
          this.formData.allowanceForStart +
          this.formData.returnValuePerQuarter *
            this.formData.returnRatePerQuarter /
            300
        );
      },
      names() {
        return (
          this.formData.category &&
          this.formData.category.reduce((sum, item) => {
            return sum + item.name;
          }, "")
        );
      }
    },
    watch: {
      cost() {
        this.drawCostChart();
      },
      income() {
        this.drawIncomeChart();
      },
      allowance() {
        this.drawIncomeChart();
      },
      "formData.category.length"() {
        this.drawIncomeChart();
      },
      names() {
        this.drawIncomeChart();
      }
    },
    methods: {
      drawCostChart() {
        this.costChart.setOption({
          title: {
            text: "支出详情",
            x: "center"
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            left: "left",
            data: ["租金支出", "人力支出", "杂费支出"]
          },
          series: [
            {
              name: "支出详情",
              type: "pie",
              radius: "55%",
              center: ["50%", "60%"],
              data: [
                { value: this.laborCost, name: "人力支出" },
                { value: this.formData.otherCost, name: "杂费支出" },
                { value: this.rent, name: "租金支出" }
              ]
            }
          ]
        });
      },
      drawIncomeChart() {
        this.incomeChart.setOption({
          title: {
            text: "收入详情",
            x: "center"
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            left: "left",
            data: ["月季返总额", "月季度店补总额", "开业支持"].concat(
              ...this.formData.category.map(item => item.name)
            )
          },
          series: [
            {
              name: "收入详情",
              type: "pie",
              radius: "55%",
              center: ["50%", "60%"],
              data: [
                {
                  name: "月季返总额",
                  value: Math.round(
                    this.formData.returnValuePerQuarter *
                      this.formData.returnRatePerQuarter /
                      300
                  )
                },
                {
                  name: "月季度店补总额",
                  value: Math.round(this.formData.allowancePerQuarter / 3)
                },
                {
                  name: "开业支持",
                  value: this.formData.allowanceForStart
                }
              ].concat(
                this.formData.category.map(item => {
                  return {
                    name: item.name,
                    value:
                      this.formData.sales *
                        item.saleRate *
                        item.grossProfitRate /
                        10000 +
                      this.formData.sales *
                        item.saleRate *
                        item.stableReturnRate /
                        10000 +
                      this.formData.sales *
                        item.saleRate *
                        item.returnValuePerMonth *
                        this.formData.returnRatePerMonth /
                        1000000
                  };
                })
              )
            }
          ]
        });
      }
    },
    mounted() {
      this.costChart = echarts.init(document.getElementById("costChart"));
      this.incomeChart = echarts.init(document.getElementById("incomeChart"));
      window.addEventListener("resize", () => {
        this.costChart.resize();
        this.incomeChart.resize();
      });
    }
  };
</script>

<style scoped>
  .chart {
    display: block;
    width: 380px;
    height: 300px;
  }
</style>



