<template>
  <div>
    <p>名称：
      <span class="total">{{thousands(formData.name)}}</span>
    </p>
    <p>销售总收入：
      <span class="total">{{thousands(income)}}</span>
    </p>
    <p>总补贴：
      <span class="total">+{{thousands(allowance)}}</span>
    </p>
    <p>总成本：
      <span class="total">-{{thousands(cost)}}</span>
    </p>
    <hr />
    <p>总盈亏：
      <span class="total" :class="income+allowance-cost>=0?'green':'red'">{{thousands(income+allowance-cost)}}</span>
    </p>
  </div>
</template>

<script>
  export default {
    props: {
      formData: Object,
      step: Number
    },
    computed: {
      cost() {
        return (
          this.formData.area * this.formData.costPerArea +
          this.formData.personNumber * this.formData.costPerPerson +
          this.formData.otherCost
        );
      },
      income() {
        if (this.formData.category) {
          return this.formData.category.reduce(
            (sum, item) =>
              sum +
              this.formData.sales * item.saleRate * item.grossProfitRate / 10000 +
              this.formData.sales *
                item.saleRate *
                item.stableReturnRate /
                10000 +
              this.formData.sales *
                item.saleRate *
                item.returnValuePerMonth *
                this.formData.returnRatePerMonth /
                1000000,
            this.formData.returnValuePerQuarter *
              this.formData.returnRatePerQuarter /
              300
          );
        } else {
          return 0;
        }
      },
      allowance() {
        return (
          this.formData.allowancePerQuarter / 3 + this.formData.allowanceForStart
        );
      }
    },
    methods: {
      thousands(num) {
        if (typeof num === "number" && num === num) {
          let isNegative = num < 0;
          num = String(Math.abs(num));
          let pos = num.indexOf(".", 1),
            res = pos > 0 ? num.substring(0, pos) : num,
            result = [];
          if (res.length < 4) {
            result.unshift(res);
          } else {
            for (let i = res.length; i > 0; i -= 3) {
              if (i > 3) {
                result.unshift(res.substr(i - 3, 3));
              } else {
                result.unshift(res.substr(0, i));
              }
            }
          }
          return (isNegative ? "-" : "") + result.join(",");
        } else {
          return num;
        }
      }
    }
  };
</script>

<style scoped>
  .green {
    color: green;
  }
  .red {
    color: red;
  }
  .total {
    position: absolute;
    right: 10%;
    font-size: 15px;
    font-weight: bold;
  }
</style>



